.button {
    border: 2px solid #0d6a9c;
    padding: 10px 35px;
    background: #0D6A9C;
    border-radius: 3px;
    color: #ffffff;
    cursor: pointer;
    font-size: 16px;
    font-weight: bold;
    transition: 0.3s;
}

.button:hover{
    background: #0d82bc;
}

.button:disabled{
    background: #849794;
    border-color: #849794;
    cursor: not-allowed;
    pointer-events: none;
}